


















import { Component, Vue, Prop } from 'vue-property-decorator'

import { roomPlayService } from '@/services/roomplay-service'
import { charactersService } from '@/services/characters-service'
import { userService } from '@/services/user-service'

import { CharactersDto, RoomPlayDto } from '@/models'

@Component({
  components: {
  }
})
/* eslint-disable */
export default class ListRoom extends Vue {

  @Prop() id?: any;
  private status : number = 0;

  private isLoading: boolean = false;

  private listRoomPlay: any = '';
  private listChaId: any[] = [];
  private chaId: number = 0;

  private roomPlayDto: RoomPlayDto = new RoomPlayDto();
  private charactersDTO: CharactersDto = new CharactersDto();
  private userId: number = 0;

  private chaExist?: boolean = false;
  
  private mounted() {
    this.isLoading = true;
    this.getListRoomPlay();
    this.getCurrentUser();
    setTimeout(() => {
      this.getCurrentCharacters()
      .finally(() => {
        if (!this.chaExist) {
          this.getRandomCharacters();
          this.getCharactersByChaId();
        }
      })

      this.isLoading = false;
    }, 2000);
  }

  private getListRoomPlay() {
    roomPlayService.getListByRoomIdAndStatus(this.id, this.status)
    .then((res: any) => {
      this.listRoomPlay = res.data;
      this.listRoomPlay.forEach((element: any) => {
        this.listChaId.push(element.chaId);
      });
    })
  }
  
  private getCurrentCharacters(): Promise<any> {
      return new Promise((resolve, reject) => {
        roomPlayService.getRPByRoomIdAndStatus(this.id, 1, this.userId)
        .then((res: any) => {
          if (res.data) {
            this.chaExist = true;
            charactersService.getCharactersById(res.data.chaId)
            .then((res: any) => {
              this.charactersDTO = res.data;
            })
          }
          else {
            this.chaExist = false;
          }
        })
        .finally(() => resolve(true))
    })
  }

  private getRandomCharacters() {
    this.chaId = this.listChaId[Math.floor(Math.random() * this.listChaId.length)];
  }

  private getCharactersByChaId() {
    charactersService.getCharactersById(this.chaId)
    .then((res: any) => {
      this.charactersDTO = res.data;
      this.prUpdateCharacters();
    })
    .finally(() => {
        this.updateCharaters();
      })
  }

  private getCurrentUser() {
      let temp: any = localStorage.getItem('userId') ? localStorage.getItem('userId') : '';
      userService.getUserById(temp)
      .then((res: any) => {
        this.userId = Number(res.data.id);
      })
  }

  private prUpdateCharacters() {
    let id: number = 0;
    for(let i = 0; i < this.listRoomPlay.length; i++) {
      if (this.listRoomPlay[i].chaId === this.chaId) {
          id = this.listRoomPlay[i].id;
          break;
      }
    }
    this.$set(this.roomPlayDto, 'id', id);
    this.$set(this.roomPlayDto, 'chaId', this.chaId);
    this.$set(this.roomPlayDto, 'roomId', this.id);
    this.$set(this.roomPlayDto, 'status', 1);
    this.$set(this.roomPlayDto, 'userId', this.userId);
  }

  private updateCharaters() {
    roomPlayService.updateRoomplay(this.roomPlayDto)
    .then((res) => {
    })
  }

}
